import Topic from "./Topic";
import useFetch from "./useFetch";

const Topics = () => {

    const { data, isPending, error } = useFetch('https://www.reddit.com/r/all.json');

    return (
        <section className='topics'>
            { error && <div>{ error } </div>}
            { isPending && <div>Loading...</div>}
            { data && <Topic topics={data.data.children}/>}
        </section>
    );
}
 
export default Topics;