import { Link } from 'react-router-dom';

const TopicInfo = ({topic}) => {

    const calculateDate = () => {
        const date1 = new Date();
        const date2 = topic.data.created * 1000;
        
        const differenceInTime = date1.getTime() - date2;
        const differenceInDays = differenceInTime / (1000 * 3600 * 24);
        if (differenceInDays < 1) {
            const roundedNum = Math.trunc(differenceInDays * 24)
            if (roundedNum === 1) return 'submitted ' + roundedNum + ' hour ago'
            else return 'submitted ' + roundedNum + ' hours ago';
        }
        else {
            const roundedNum = Math.trunc(differenceInDays)
            if (roundedNum === 1) return 'submitted ' + roundedNum + ' day ago'
            else return 'submitted ' + roundedNum + ' days ago';
        }
    }

    return (
        <div className="topic-info">
            <div className='topic-info-item info-title'><Link to=''>{ topic.data.title }</Link></div>
            <div className='topic-info-item info-additional flex'>
                <div>{ calculateDate() } by <Link to=''>{ topic.data.author }</Link></div>
                <div>to <Link to="">{ topic.data.subreddit_name_prefixed }</Link></div>
            </div>
            <div className='topic-info-item info-share'>
                <Link to=''>{ topic.data.num_comments } comments</Link>
            </div>
        </div>
    );
}
 
export default TopicInfo;