import TopicInfo from "./TopicInfo";
import { Link } from 'react-router-dom';

const Topic = ({topics}) => {

    const getThumbnail = (topic) => {
        const noThumbnail = ['self', 'default', 'nsfw'];
        if (noThumbnail.includes(topic.data.thumbnail)) {
            return null;
        }
        else {
            return topic.data.thumbnail;
        }
    }

    const getUpvotes = (topic) => {
        const upvotes = topic.data.score;
        if (upvotes >= 1000) {
            const abbreviation = Math.trunc(upvotes/100);
            return abbreviation/10 + 'k';
        } else return upvotes;

    }

    return (
        <div className='topic-container'>
            {topics.map((topic, index) => (
                <div className="topic flex" key={index}>
                    <div className='topic-item topic-id flex flex-center'>{ index + 1 }</div>
                    <div className='topic-item topic-votes flex flex-center'>{ getUpvotes(topic) }</div>
                    { !getThumbnail(topic) && <Link to=''><div className='topic-item topic-thumbnail topic-thumbnail-default flex flex-center'></div></Link> }
                    { getThumbnail(topic) && <Link to=''><div className='topic-item topic-thumbnail flex flex-center'><img src={getThumbnail(topic)} alt="topic img" /></div></Link> }
                    <TopicInfo topic={topic}/>
                </div>
            ))}
        </div>
    );
}

export default Topic;